.about {
  padding: 60px 0 100px;

  @include vp-1023 {
    padding: 40px 0 80px;
  }

  @include vp-767 {
    padding: 25px 0 80px;
  }
}

.about__container {
  display: grid;
  grid-template-columns: 150px auto;
  column-gap: 100px;

  @include vp-1023 {
    column-gap: 20px;
  }

  @include vp-767 {
    display: flex;
    flex-direction: column;
  }
}

.about__decor {
  display: flex;
  flex-direction: column;
  align-items: center;
  align-self: flex-start;

  transform: rotate(-180deg);
  overflow: hidden;

  @include vp-767 {
    display: none;
  }
}

.about__decor span {
  margin-bottom: 10px;
  font-weight: 700;
  font-size: 156px;
  line-height: 220px;
  color: transparent;
  writing-mode: vertical-rl;

  background-image: url("../../img/about-bg.webp");
  background-clip: text;

  user-select: none;
}

.about__info {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-self: flex-start;
}

.about__info h3 {
  margin: 0;
  margin-bottom: 5px;

  font-size: 36px;
  line-height: 51px;
  font-weight: 400;

  @include vp-767 {
    font-size: 28px;
    line-height: 140.62%;
  }
}

.about__info p {
  margin: 10px 0;

  font-family: 'PT Mono';
  line-height: 22px;

  @include vp-767 {
    font-size: 14px;
    line-height: 134.4%;

    & span {
      display: none;
    }
  }
}

.about__button {
  margin-top: 30px;
  width: 180px;
  padding: 13px;

  display: flex;
  justify-content: center;

  font-size: 14px;
  line-height: 16px;

  @include vp-1023 {
    margin-top: 10px;
  }

  @include vp-767 {
    padding: 8px 15px 10px;
    width: 160px;
  }
}
